import React, { useState } from 'react'
import { useDispatch } from 'react-redux'
import commentService from '../services/comments'

const CommentForm = ({createComm}) => {
  //const [newAuthor, setNewAuthor] = useState('')
  const [newContent, setNewContent] = useState('')
  const dispatch = useDispatch()

/*   const handleAuthorChange = (event) => {
    setNewAuthor(event.target.value)
  } */

  const handleContentChange = (event) => {
    setNewContent(event.target.value)
  }


  const addComment = async (event) => {
    event.preventDefault()
    dispatch(createComm({content: newContent}))

    //setNewAuthor('')
    setNewContent('')

  }

  return (
    <div>
      <h2>Post a new comment</h2>
      <br />
      <form onSubmit={addComment} className='commentForm'>
          Comment:
          <textarea id='comment' value={newContent} onChange={handleContentChange} className='comment-field' />
        <div>
          <button type='submit' className='submit-button'>Post comment</button>
        </div>
      </form>
    </div>)

}

export default CommentForm