import React, { useState } from 'react'

const LoginForm = ({
  username,
  password,
  name,
  handleUsernameChange,
  handlePasswordChange,
  handleNameChange,
  handleSubmit
}) => {
  const [passwordVerifier, setPasswordVerifier] = useState('')

  const handlePassVerifierChange = (event) => {
  setPasswordVerifier(event.target.value)
  }

  const passIsSame = (password === passwordVerifier)
  const submitButtonEnabled = (passIsSame && username.length > 3 && password.length > 3)


  return (
    <div>
      <h2>
        Sign up
      </h2>
      <form onSubmit={handleSubmit} className='commentForm'>
        <div>
          Username:
        <br />
          <input id='signup-username'
            value={username}
            onChange={handleUsernameChange}
            className='signup-login-field'
          />
        </div>
        <div>
          Name:
          <br />
          <input id='signup-name'
            type='name'
            value={name}
            onChange={handleNameChange}
            className='signup-login-field'
          />
        </div>
        <div>
          Password:
          <br />
          <input id='signup-pass'
            type='password'
            autoComplete='new-password'
            value={password}
            onChange={handlePasswordChange}
            className='signup-login-field'
          />
        </div>
        <div>
          Confirm password:
          {!passIsSame && (
            <div className='password-verification-notice'>Passwords don't match!</div>
          )}
          <br />
          <input id='signup-pass-verify'
            type='password'
            autoComplete='new-password'
            value={passwordVerifier}
            onChange={handlePassVerifierChange}
            className='signup-login-field'
          />
        </div>
        <button id='signup-button' type='submit' className='submit-button' disabled={!submitButtonEnabled}>Sign up</button>
      </form>
    </div>
  )
}

export default LoginForm
